import { Box, Divider, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import HomeIcon from '@mui/icons-material/Home';
import axios from 'axios';
import { useRecoilValue } from 'recoil';
import { useEffect, useState } from 'react';
import theme from '../../../styles/theme';
import Flex from '../../UI/atoms/Flex';
import Spacer from '../../UI/atoms/Spacer';
import Text from '../../UI/atoms/Text';
import RecommendedCard from './RecommendedCard';
import { ThemeData } from '../../../types/themeData';
import requestStringAtom from '../../../recoil/atoms/requsetStringAtom';

export default function Recommender() {
  const navigate = useNavigate();
  const requestString = useRecoilValue(requestStringAtom);
  const [recommendResults, setRecommendResults] = useState<ThemeData[]>([]);

  axios.defaults.withCredentials = true;

  const requestResult = () => {
    axios
      .get(`http://localhost:8000/recommend/${requestString}`, {
        withCredentials: true,
      })
      .then(function (response) {
        console.log(response);
        const tmpResults: ThemeData[] = response.data
          .recommended as ThemeData[];
        setRecommendResults(tmpResults);
      })
      .catch(function (error) {
        console.log(error, 'error');
      });
  };

  useEffect(() => {
    if (requestString) {
      requestResult();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Box>
      <Flex
        sx={{
          width: 'calc(100vw - 40px)',
        }}
        direction="column"
      >
        <Spacer y={12} />
        <Flex sx={{ alignItems: 'center', justifyContent: 'space-between' }}>
          <IconButton onClick={() => navigate('/searcher')}>
            <ArrowBackIcon
              sx={{ color: theme.colors.BLACK, paddingTop: '3px' }}
            />
          </IconButton>
          <Text lg bold>
            추천 목록
          </Text>
          <IconButton onClick={() => navigate('/')}>
            <HomeIcon sx={{ color: theme.colors.COOL_PURPLE80 }} />
          </IconButton>
        </Flex>
        <Divider />

        <Flex
          direction="column"
          sx={{
            justifyContent: 'center',
            height: 'calc(100vh - 110px)',
            overflowY: 'scroll',
            '::-webkit-scrollbar': {
              display: 'none',
            },
          }}
          px={0}
          gap="16px"
        >
          {recommendResults.map((recommendResult) => (
            <RecommendedCard
              name={recommendResult.name}
              image={recommendResult.image}
              reasons={recommendResult.reasons}
              difficulty={recommendResult.difficulty}
              member={recommendResult.member}
              timeLimit={recommendResult.timeLimit}
              type={recommendResult.type}
            />
          ))}
        </Flex>
      </Flex>
    </Box>
  );
}
