import { Box, Button, Card, Divider, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useEffect, useState } from 'react';
import Grid2 from '@mui/material/Unstable_Grid2';
import TuneIcon from '@mui/icons-material/Tune';
import axios from 'axios';
import Flex from '../../UI/atoms/Flex';
import PlusCard from './PlusCard';
import Spacer from '../../UI/atoms/Spacer';
import Text from '../../UI/atoms/Text';
import ThemeCard from './ThemeCard';
import selectedThemesAtom from '../../../recoil/atoms/selectedThemesAtom';
import theme from '../../../styles/theme';
import PreferenceDialog from './PreferenceDialog';
import ThemeDataChips from '../ThemeDataChips';
import preferenceDifficultyAtom from '../../../recoil/atoms/preferenceDifficultyAtom';
import preferenceMemberAtom from '../../../recoil/atoms/preferenceMemberAtom';
import preferenceRoomTypeAtom from '../../../recoil/atoms/preferenceRoomTypeAtom';
import preferenceTimelimitAtom from '../../../recoil/atoms/preferenceTimelimitAtom';
import requestStringAtom from '../../../recoil/atoms/requsetStringAtom';
import { ThemeData } from '../../../types/themeData';
import { EscapeTheme } from '../../../types/escapeTheme';
import themeListAtom from '../../../recoil/atoms/themeListAtom';

export default function Searcher() {
  const navigate = useNavigate();
  const selectedThemes = useRecoilValue(selectedThemesAtom);
  const preferenceDifficulty = useRecoilValue(preferenceDifficultyAtom);
  const preferenceMember = useRecoilValue(preferenceMemberAtom);
  const preferenceRoomType = useRecoilValue(preferenceRoomTypeAtom);
  const preferenceTimeLimit = useRecoilValue(preferenceTimelimitAtom);
  const setRequestString = useSetRecoilState(requestStringAtom);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const setThemeList = useSetRecoilState(themeListAtom);

  const requestResult = () => {
    axios
      .get(`http://localhost:8000/themes`, {
        withCredentials: true,
      })
      .then(function (response) {
        const themes: EscapeTheme[] = response.data.themeList;
        setThemeList(themes);
      })
      .catch(function (error) {
        console.log(error, 'error');
      });
  };

  useEffect(() => {
    requestResult();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Box>
      <Flex
        sx={{
          width: 'calc(100vw - 40px)',
        }}
        direction="column"
      >
        <Spacer y={12} />
        <Flex center>
          <IconButton onClick={() => navigate('/')} sx={{ left: 0 }}>
            <ArrowBackIcon
              sx={{ color: theme.colors.BLACK, paddingTop: '3px' }}
            />
          </IconButton>
          <Text lg bold sx={{ margin: 'auto', paddingRight: '40px' }}>
            경험해본 테마들을 추가해주세요
          </Text>
        </Flex>
        <Divider />

        <Grid2 container>
          <Grid2 xs={2} px="10px">
            <Spacer y={16} />
            <Card
              sx={{
                backgroundColor: theme.colors.COOL_PURPLE40,
                maxWidth: '150px',
              }}
            >
              <Flex
                px={0}
                direction="column"
                gap="10px"
                sx={{ minWidth: '90px' }}
                center
              >
                <Spacer y={8} />
                <Text lg bold color="WHITE">
                  선호 세팅
                </Text>
                <ThemeDataChips
                  difficulty={preferenceDifficulty}
                  member={preferenceMember}
                  timeLimit={preferenceTimeLimit}
                  type={preferenceRoomType}
                />
                <IconButton
                  onClick={() => setIsDialogOpen(true)}
                  sx={{ padding: 0 }}
                >
                  <TuneIcon />
                </IconButton>
                <Spacer y={16} />
              </Flex>
            </Card>
          </Grid2>

          <Grid2 xs>
            <Spacer y={16} />
            <Flex
              direction="column"
              sx={{
                height: 'calc(100vh - 110px)',
                overflowY: 'scroll',
                '::-webkit-scrollbar': {
                  display: 'none',
                },
              }}
              px={0}
              gap="8px"
            >
              {selectedThemes.map((selectedTheme) => (
                <ThemeCard
                  label={selectedTheme.label}
                  avgRate={selectedTheme.avgRate}
                  location={selectedTheme.location}
                />
              ))}
              <PlusCard />
              <Spacer y={48} />
            </Flex>
          </Grid2>
        </Grid2>
        <Button
          variant="bordered"
          sx={{
            width: '100%',
          }}
          onClick={() => {
            const initialString = '';
            setRequestString(
              selectedThemes.reduce(
                (prev, selectedTheme) =>
                  prev.concat(
                    selectedTheme.label.concat(
                      ',',
                      selectedTheme.avgRate.toString(),
                    ),
                    ',',
                  ),
                initialString,
              ),
            );
            navigate('/recommender');
          }}
        >
          추천받기
        </Button>
      </Flex>
      <PreferenceDialog
        isOpen={isDialogOpen}
        onClose={() => setIsDialogOpen(false)}
      />
    </Box>
  );
}
