import {
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Tooltip,
} from '@mui/material';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import StarIcon from '@mui/icons-material/Star';
import { useEffect, useState } from 'react';
import axios from 'axios';
import Flex from '../../UI/atoms/Flex';
import HeaderText from '../../UI/atoms/HeaderText';
import theme from '../../../styles/theme';
import Spacer from '../../UI/atoms/Spacer';
import { ThemeData } from '../../../types/themeData';
import ThemeDataChips from '../ThemeDataChips';
import ReasonChip from './ReasonChip';

export default function RecommendedCard({
  name,
  image,
  reasons,
  difficulty,
  member,
  timeLimit,
  type,
}: ThemeData) {
  const [isFavorite, setIsFavorite] = useState(false);
  const [imageBase64, setImageBase64] = useState('');

  const requestResult = () => {
    axios
      .get(`http://localhost:8000/images/${image}`, {
        withCredentials: true,
      })
      .then(function (response) {
        setImageBase64(response.data);
      })
      .catch(function (error) {
        console.log(error, 'error');
      });
  };

  useEffect(() => {
    if (image) {
      requestResult();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Card
      sx={{
        borderRadius: '16px',
      }}
    >
      <CardContent>
        <Flex px={0}>
          <CardMedia
            component="img"
            image={`data:image/jpeg;base64,${imageBase64}`}
            alt="none"
            sx={{
              width: '100px',
              height: '150px',
              backgroundColor: theme.colors.GRAY20,
              borderRadius: '16px',
            }}
          />
          <Flex
            pr={0}
            direction="column"
            sx={{ width: 'calc(100% - 120px)', height: '150px' }}
          >
            <Flex px={0} sx={{ justifyContent: 'space-between' }}>
              <Flex px={0} sx={{ alignItems: 'center' }}>
                <HeaderText h2>{name}</HeaderText>
                <ThemeDataChips
                  difficulty={difficulty}
                  member={member}
                  timeLimit={timeLimit}
                  type={type}
                />
              </Flex>
              <Flex px={0} gap="4px">
                <Tooltip title="사이트 바로가기">
                  <IconButton sx={{ padding: 0 }}>
                    <OpenInNewIcon />
                  </IconButton>
                </Tooltip>
                <Tooltip title="즐겨찾기 추가">
                  <IconButton
                    sx={{ padding: 0 }}
                    onClick={() => setIsFavorite((v) => !v)}
                  >
                    {isFavorite ? <StarIcon /> : <StarBorderIcon />}
                  </IconButton>
                </Tooltip>
              </Flex>
            </Flex>
            <Spacer y={12} />
            {reasons.map((reason, index) => (
              <ReasonChip
                reason={reason.recommendReason}
                rating={reason.recommendRating}
                isFirst={index === 0}
              />
            ))}
          </Flex>
        </Flex>
      </CardContent>
    </Card>
  );
}
