import { Dialog } from '@mui/material';
import { useRecoilState } from 'recoil';
import Flex from '../../UI/atoms/Flex';
import StepSlider from './StepSlider';
import preferenceDifficultyAtom from '../../../recoil/atoms/preferenceDifficultyAtom';
import preferenceMemberAtom from '../../../recoil/atoms/preferenceMemberAtom';
import preferenceRoomTypeAtom from '../../../recoil/atoms/preferenceRoomTypeAtom';
import preferenceTimelimitAtom from '../../../recoil/atoms/preferenceTimelimitAtom';
import {
  Difficulty,
  Member,
  RoomType,
  TimeLimit,
} from '../../../types/themeData';

const difficultyValue = ['하', '중', '상'];
const difficultyMarks = [
  {
    value: 10,
    label: '하',
  },
  {
    value: 20,
    label: '중',
  },
  {
    value: 30,
    label: '상',
  },
];
const memberValue = ['1인', '2인', '3인', '4인', '4인+'];
const memberMarks = [
  {
    value: 10,
    label: '1인',
  },
  {
    value: 20,
    label: '2인',
  },
  {
    value: 30,
    label: '3인',
  },
  {
    value: 40,
    label: '4인',
  },
  {
    value: 50,
    label: '4인+',
  },
];
const timeLimitValue = ['45분', '60분', '75분', '75분+'];
const timeLimitMarks = [
  {
    value: 10,
    label: '45분',
  },
  {
    value: 20,
    label: '60분',
  },
  {
    value: 30,
    label: '75분',
  },
  {
    value: 40,
    label: '75분+',
  },
];
const roomTypeValue = ['장치', '혼합', '자물쇠'];
const roomTypeMarks = [
  {
    value: 10,
    label: '장치',
  },
  {
    value: 20,
    label: '혼합',
  },
  {
    value: 30,
    label: '자물쇠',
  },
];

export default function PreferenceDialog({
  isOpen,
  onClose,
}: {
  isOpen: boolean;
  onClose: () => void;
}) {
  const [preferenceDifficulty, setPreferenceDifficulty] = useRecoilState(
    preferenceDifficultyAtom,
  );
  const [preferenceMember, setPreferenceMember] =
    useRecoilState(preferenceMemberAtom);
  const [preferenceRoomType, setPreferenceRoomType] = useRecoilState(
    preferenceRoomTypeAtom,
  );
  const [preferenceTimeLimit, setPreferenceTimeLimit] = useRecoilState(
    preferenceTimelimitAtom,
  );

  return (
    <Dialog open={isOpen} onClose={onClose}>
      <Flex py="20px" gap="8px" width="300px" direction="column">
        <StepSlider
          text="난이도"
          textColor="RED"
          values={difficultyValue}
          marks={difficultyMarks}
          defaultValue={preferenceDifficulty}
          handleChange={(_, value) => {
            setPreferenceDifficulty(
              difficultyValue[(value as number) / 10 - 1] as Difficulty,
            );
          }}
        />
        <StepSlider
          text="제한인원"
          textColor="GREEN"
          values={memberValue}
          marks={memberMarks}
          defaultValue={preferenceMember}
          handleChange={(_, value) => {
            setPreferenceMember(
              memberValue[(value as number) / 10 - 1] as Member,
            );
          }}
        />
        <StepSlider
          text="제한시간"
          textColor="BLUE"
          values={timeLimitValue}
          marks={timeLimitMarks}
          defaultValue={preferenceTimeLimit}
          handleChange={(_, value) => {
            setPreferenceTimeLimit(
              timeLimitValue[(value as number) / 10 - 1] as TimeLimit,
            );
          }}
        />
        <StepSlider
          text="유형"
          textColor="YELLOW"
          values={roomTypeValue}
          marks={roomTypeMarks}
          defaultValue={preferenceRoomType}
          handleChange={(_, value) => {
            setPreferenceRoomType(
              roomTypeValue[(value as number) / 10 - 1] as RoomType,
            );
          }}
        />
      </Flex>
    </Dialog>
  );
}
